import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {AuthenticationProvider} from "../../providers/authentication/authentication";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";


@IonicPage()
@Component({
  selector: 'page-reset-password01',
  templateUrl: 'reset-password01.html',
})
export class ResetPassword01Page {
  resetPassword01Error;
  captchaID;
  imageData;
  formVal = {userID: '', accountNo: '', chKey: ''};

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public maskTokenUtil: MaskTokenProvider,
              public storage: LocalStorageProvider,
              public alertCtrl: AlertController,
              public authenticationService: AuthenticationProvider,
              public cryptoUtil: CryptoParamUtilProvider,
              public tokenService: TokenServiceProvider,
              public gaProvider: GoogleAnalyticsProvider,
              public autuProvider: AuthenticationProvider) {

    this.loadCaptchaImage();
  }

  loadCaptchaImage = function () {

    this.autuProvider.getCaptcha()
      .then(data => {
        this.onCaptchaFetchComplete(data);
      });
  };

  loadAnotherCaptchaImage = function () {
    this.resetPassword01Error = "";
      this.autuProvider.getCaptcha()
      .then(data => {
        this.onCaptchaFetchComplete(data);
      });
  };

  onCaptchaFetchComplete = function (data) {
    // console.log(data);
    if (data.status == 0) {
      this.resetPassword01Error = "Service unreachable, please try again later.";
    }

    else if (data.statusCode == 404) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else if (data.captchaImg.captchaId != null) {
      this.captchaID = data.captchaImg.captchaId;
      // console.log("Captcha id " + data.captchaImg.captchaId);
      this.imageData = "data:image/jpeg;base64," + data.captchaImg.data;

    }

    else if (data.captchaImg.captchaId = null) {
      this.resetPassword01Error = "Captcha generation error.";
      return;
    }

    else {
      this.resetPassword01Error = "System Error, Please Contact with Administrator.";
    }
  };

  ResetPassword01Action = function () {
    this.resetPassword01Error = "";
    // console.log(this.formVal);
    let flag = true;

    if (this.formVal.userID == "" || typeof(this.formVal.userID) == "undefined") {
      this.resetPassword01Error = "Please Enter User ID";
      flag = false;
      return;
    }

    if (this.formVal.accountNo == "" || typeof(this.formVal.accountNo) == "undefined") {
      this.resetPassword01Error = "Please Enter Account No.";
      flag = false;
      return;
    }

    if (this.formVal.chKey == "" || typeof(this.formVal.chKey) == "undefined") {
      this.resetPassword01Error = "Please Enter Challenge Key.";
      flag = false;
      return;

    }
    let encCaptchaID = this.cryptoUtil.getEncryptVal(this.captchaID);
    // console.log("enc id"+encCaptchaID);
    if (flag) {
      let encUser = this.cryptoUtil.getEncryptVal(this.formVal.userID);
      let encAccountNo = this.cryptoUtil.getEncryptVal(this.formVal.accountNo);

      let encChKey = this.cryptoUtil.getEncryptVal(this.formVal.chKey);
      this.autuProvider.initResetProfilePwdRequest(encUser,encAccountNo,encCaptchaID,encChKey)
        .then(data => {
          this.initiateResetProfilePassword(data);
        });
    }
  };

    initiateResetProfilePassword = function (data) {
      // console.log(data);
      this.resetPassword01Error = "";
      if (data.status == 0) {
        this.resetPassword01Error = "Service unreachable, please try again later.";
      }

      else if (data.statusCode == 404) {
        this.navCtrl.setRoot('ExpirePage');
      }

      else if (data.resetPwdResponse.status==100) {
        this.formVal = {userID: '', accountNo: '', chKey: ''};
        this.navCtrl.push('ResetPassword02Page',{'initToken': data.resetPwdResponse.initToken,'passPolicy' : data.resetPwdResponse.passwordPolicy});
      }

      else if (data.resetPwdResponse.status==999) {
        this.resetPassword01Error = data.resetPwdResponse.note;
        this.formVal = {userID: '', accountNo: '', chKey: ''};
        this.loadCaptchaImage();
        return;
      }

      else {
        this.resetPassword01Error = "System Error, Please Contact with Administrator.";
      }
    };

    ionViewDidLoad()
    {
      // console.log('ionViewDidLoad ResetPassword01Page');
    }

  }



